import React, { useState } from "react";
import "./downloadwgit_block.css";

type DownloadWGitExamples = {
  clone_url: string;
  visit_url: string;
};

const DownloadWithGitBlock: React.FC<DownloadWGitExamples> = ({clone_url, visit_url}) => {
      const [copied, setCopied] = useState(false);

      const copyToClipboard = (cmd: string) => {
        navigator.clipboard.writeText("git " + cmd).then(() => {
          setCopied(true);
          setTimeout(() => setCopied(false), 2000);
        });
      };
  return (
    <div className="downloadwgit_block">
      <div className="withgit_title">
        <svg
          width="24"
          height="24"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M12 0.296997C5.37 0.296997 0 5.67 0 12.297C0 17.6 3.438 22.097 8.205 23.682C8.805 23.795 9.025 23.424 9.025 23.105C9.025 22.82 9.015 22.065 9.01 21.065C5.672 21.789 4.968 19.455 4.968 19.455C4.422 18.07 3.633 17.7 3.633 17.7C2.546 16.956 3.717 16.971 3.717 16.971C4.922 17.055 5.555 18.207 5.555 18.207C6.625 20.042 8.364 19.512 9.05 19.205C9.158 18.429 9.467 17.9 9.81 17.6C7.145 17.3 4.344 16.268 4.344 11.67C4.344 10.36 4.809 9.29 5.579 8.45C5.444 8.147 5.039 6.927 5.684 5.274C5.684 5.274 6.689 4.952 8.984 6.504C9.944 6.237 10.964 6.105 11.984 6.099C13.004 6.105 14.024 6.237 14.984 6.504C17.264 4.952 18.269 5.274 18.269 5.274C18.914 6.927 18.509 8.147 18.389 8.45C19.154 9.29 19.619 10.36 19.619 11.67C19.619 16.28 16.814 17.295 14.144 17.59C14.564 17.95 14.954 18.686 14.954 19.81C14.954 21.416 14.939 22.706 14.939 23.096C14.939 23.411 15.149 23.786 15.764 23.666C20.565 22.092 24 17.592 24 12.297C24 5.67 18.627 0.296997 12 0.296997Z"
            fill="white"
          ></path>
        </svg>
        <p className="down_with_git_text">Download the Project with Github</p>
      </div>
      <div className="content_wgithub_block">
        <div className="block_wthcomm">
          <p className="wthcomm_text">
            Clone the Classic Navigation Menu with one Git command:
          </p>
          <div className="wrap">
            <div className="terminal">
              <hgroup className="headofterminal">
                <p className="title_terminal">
                  <svg
                    width="16px"
                    height="16px"
                    aria-hidden="true"
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 24 24"
                    strokeLinejoin="round"
                    strokeLinecap="round"
                    strokeWidth="2"
                    stroke="currentColor"
                    fill="none"
                  >
                    <path d="M7 15L10 12L7 9M13 15H17M7.8 21H16.2C17.8802 21 18.7202 21 19.362 20.673C19.9265 20.3854 20.3854 19.9265 20.673 19.362C21 18.7202 21 17.8802 21 16.2V7.8C21 6.11984 21 5.27976 20.673 4.63803C20.3854 4.07354 19.9265 3.6146 19.362 3.32698C18.7202 3 17.8802 3 16.2 3H7.8C6.11984 3 5.27976 3 4.63803 3.32698C4.07354 3.6146 3.6146 4.07354 3.32698 4.63803C3 5.27976 3 6.11984 3 7.8V16.2C3 17.8802 3 18.7202 3.32698 19.362C3.6146 19.9265 4.07354 20.3854 4.63803 20.673C5.27976 21 6.11984 21 7.8 21Z"></path>
                  </svg>
                  Terminal
                </p>

                <button
                  className="copy_button"
                  onClick={() => copyToClipboard(clone_url)}
                >
                  <span className="copy_text">
                    {copied ? "Copied" : "Copy"}
                  </span>
                </button>
              </hgroup>

              <div className="body">
                <pre className="pre">
                  {" "}
                  <code>-&nbsp;</code>
                  <code>git&nbsp;</code>
                  <code className="cmd" data-cmd={clone_url}></code>
                </pre>
              </div>
            </div>
          </div>
        </div>
        <div className="rght_side_wbtn">
          <h3 className="contributions_bl"> Contributions are welcome!</h3>
          <p>
            Feel free to open issues or submit pull requests to improve this
            project.
          </p>
          <a href={visit_url} className="wtc_onhub project_btn">
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M12 0.296997C5.37 0.296997 0 5.67 0 12.297C0 17.6 3.438 22.097 8.205 23.682C8.805 23.795 9.025 23.424 9.025 23.105C9.025 22.82 9.015 22.065 9.01 21.065C5.672 21.789 4.968 19.455 4.968 19.455C4.422 18.07 3.633 17.7 3.633 17.7C2.546 16.956 3.717 16.971 3.717 16.971C4.922 17.055 5.555 18.207 5.555 18.207C6.625 20.042 8.364 19.512 9.05 19.205C9.158 18.429 9.467 17.9 9.81 17.6C7.145 17.3 4.344 16.268 4.344 11.67C4.344 10.36 4.809 9.29 5.579 8.45C5.444 8.147 5.039 6.927 5.684 5.274C5.684 5.274 6.689 4.952 8.984 6.504C9.944 6.237 10.964 6.105 11.984 6.099C13.004 6.105 14.024 6.237 14.984 6.504C17.264 4.952 18.269 5.274 18.269 5.274C18.914 6.927 18.509 8.147 18.389 8.45C19.154 9.29 19.619 10.36 19.619 11.67C19.619 16.28 16.814 17.295 14.144 17.59C14.564 17.95 14.954 18.686 14.954 19.81C14.954 21.416 14.939 22.706 14.939 23.096C14.939 23.411 15.149 23.786 15.764 23.666C20.565 22.092 24 17.592 24 12.297C24 5.67 18.627 0.296997 12 0.296997Z"
                fill="white"
              ></path>
            </svg>
            <p className="text">View on Github</p>
          </a>
        </div>
      </div>
    </div>
  );
};

export default DownloadWithGitBlock;
